  padding: 10px 20px;
  background-color: wheat;
  position: sticky;
  top: 0;
  z-index: 9;
}

.home-list {
  display: flex;
  flex-wrap: wrap;
  font-size: 12px;

  &>div {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    width: 33%;
    border-radius: 5px;
    padding: 5px;
    padding-bottom: 20px;
    text-align: center;

    .img-box {
      position: relative;
      padding-top: 100%;
      width: 100%;
      background-color: beige;
    }

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    label {
      color: black;
      padding-bottom: 5px;
      word-break: break-word;
    }

    span {
      color: gray;
      word-break: break-word;
    }

    p {
      color: darkgreen;
      cursor: pointer;
    }

    p+p {
      color: red;
    }

    i {
      padding-top: 12px;
    }

    .stars {
      box-sizing: border-box;
      padding: 20px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row;

      b {
        display: block;
        width: 15%;
        height: 0;
        padding-top: 15%;
        border-radius: 50%;
      }
    }


  }
}
@media screen and (min-width:1200px) {
  .home-list>div{
    width: 10%;
  }
}
@media screen and (max-width:1200px) {
  .home-list>div{
    width: calc(100%/6);
  }
}
